import React from "react";
import { Grid, useMediaQuery, useTheme } from "@material-ui/core";
import {
	makeStyles,
	createTheme,
	ThemeProvider,
} from "@material-ui/core/styles";
import { DndProvider } from "react-dnd";
import Backend from "react-dnd-html5-backend";

import SidePanel from "./Panels/SidePanel";
import MainPanel from "./Panels/MainPanel";
import Toolbar from "./Toolbar/ToolbarContainer";
import StudioProvider, { useStore } from "./store/context";
import {
	fetchLanguages,
	fetchUserPreferences,
	getEnableAppBuilder,
} from "./Toolbar/api";

const useStyles = makeStyles({
	container: {
		display: "flex",
		height: "100%",
		flexDirection: "column",
		flexWrap: "nowrap",
	},
	toolbarWrapper: {
		flex: 0,
		display: "flex",
		flexWrap: "wrap",
	},
	panelContainer: {
		display: "flex",
		flex: 1,
		overflow: "hidden",
	},
	sidePanel: {
		height: "100%",
		".modern-dark &": {
			backgroundColor: "#202124",
		},
	},
	mainPanel: {
		height: "100%",
		overflow: "auto",
		".modern-dark &": {
			backgroundColor: "#202124",
		},
	},
});

const theme = createTheme({
	typography: {
		fontFamily: '"Open Sans", "Helvetica Neue", Helvetica, Arial, sans-serif',
	},
});

function AppContent() {
	const classes = useStyles();
	const theme = useTheme();
	const { state, update } = useStore();
	const { sidePanel = true } = state;
	const isResponsive = useMediaQuery(theme.breakpoints.down("sm"));
	const gridRef = React.useRef();

	React.useEffect(() => {
		fetchLanguages().then((result) => {
			update((draft) => {
				draft.languageList = [...result];
			});
		});
		fetchUserPreferences().then((result) => {
			if (result && result.theme) {
				document.body.className = result.theme;
			}
		});

		getEnableAppBuilder().then((res) => {
			update((draft) => {
				draft.enableAppBuilder = res;
			});
		});
	}, [update]);

	React.useEffect(() => {
		update((draft) => {
			draft.sidePanel = !isResponsive;
		});
	}, [isResponsive, update]);

	React.useEffect(() => {
		const grid = gridRef.current;
		grid && grid.focus();
	}, []);

	return (
		<Grid container className={classes.container} tabIndex={0} ref={gridRef}>
			<Grid item xs={12} className={classes.toolbarWrapper}>
				<Toolbar />
			</Grid>
			<Grid container className={classes.panelContainer}>
				{sidePanel && (
					<Grid item xs={isResponsive ? 4 : 3} className={classes.sidePanel}>
						<SidePanel />
					</Grid>
				)}
				<Grid
					item
					xs={sidePanel ? (isResponsive ? 8 : 9) : 12}
					className={classes.mainPanel}
				>
					<MainPanel />
				</Grid>
			</Grid>
		</Grid>
	);
}

export default function App() {
	return (
		<StudioProvider>
			<DndProvider backend={Backend}>
				<ThemeProvider theme={theme}>
					<AppContent />
				</ThemeProvider>
			</DndProvider>
		</StudioProvider>
	);
}
