import React from "react";
import { Tabs, Tab, Paper, Grid } from "@material-ui/core";
import { makeStyles } from "@material-ui/core/styles";
import { ListAlt, PlaylistAdd } from "@material-ui/icons";
import classNames from "classnames";

import AddPanelView from "./Tabs/AddPanelView";
import PropertiesView from "./Tabs/PropertiesView";
import { useStore } from "../store/context";
import { translate } from "../utils";

const useStyles = makeStyles({
	root: {
		boxShadow: "1px 2px 8px 0px rgb(1 1 1 / 75%) !important",
		backgroundColor: "#2f4050",
		".modern-dark &": {
			backgroundColor: "#1b1b1b",
		},
		height: '100%',
		width: '100%',
    borderTop: '1px solid #2f4050',
	},
	tab: {
		minHeight: "fit-content",
		minWidth: "fit-content",
		textTransform: "none",
		color: "#fff",
		fontWeight: "600",
		backgroundColor: "#293846",
		".modern-dark &": {
			color: "#fff",
			backgroundColor: "#1b1b1b",
		},
		"&>span": {
			flexDirection: "row",
			"& > svg": {
				marginBottom: "0px !important",
				marginRight: 5,
			},
		},
	},
	tabs: {
		backgroundColor: "#293846",
		minHeight: "42px !important",
		borderTopRightRadius: 5,
		".modern-dark &": {
			backgroundColor: "#1b1b1b",
		},
	},
	container: {
		backgroundColor: "#2f4050",
		height: `calc(100% - 42px)`,
		overflow: "auto",
		scrollbarWidth: "thin",
		borderBottomRightRadius: 5,
		".modern-dark &": {
			backgroundColor: "#1b1b1b",
		},
	},
	activeTab: {
		backgroundColor: "#2f4050",
		borderBottom: "2px solid #fff",
		".modern-dark &": {
			backgroundColor: "#323232",
		},
	},
});

const tabs = [
	{ label: "Properties", icon: <ListAlt />, view: <PropertiesView /> },
	{ label: "Attributes", icon: <PlaylistAdd />, view: <AddPanelView /> },
];

const TabPanel = React.memo(function TabPanel(props) {
	const { children } = props;
	const classes = useStyles();

	return (
		<Grid className={classes.container}>
			<Grid style={{ color: "#000", height: "100%" }}>{children}</Grid>
		</Grid>
	);
});

export default React.memo(function SidePanel() {
	const classes = useStyles();
	const { state, update } = useStore();
	const { tabIndex, editWidget } = state;

	const onTabChange = (event, newValue) => {
		if (newValue === 0 && !editWidget) {
			return;
		}
		update((draft) => {
			draft.tabIndex = newValue;
		});
	};

	return (
		<Paper square className={classes.root}>
			<Tabs
				value={tabIndex}
				onChange={onTabChange}
				variant="fullWidth"
				className={classes.tabs}
				TabIndicatorProps={{
					style: {
						display: "none",
					},
				}}
			>
				{tabs.map((tab, i) => (
					<Tab
						key={i}
						className={classNames(classes.tab, {
							[classes.activeTab]: tabIndex === i,
						})}
						icon={tab.icon}
						label={translate(tab.label)}
						style={{
							color: i === 1 && !editWidget ? "#a6a6a6" : "#fff",
						}}
					/>
				))}
			</Tabs>
			{tabs.map(
				(tab, i) => tabIndex === i && <TabPanel key={i}>{tab.view}</TabPanel>
			)}
		</Paper>
	);
});
