import React from "react";
import { IconButton } from "@material-ui/core";
import { makeStyles } from "@material-ui/core/styles";
import classnames from "classnames";
import ViewSelection from "./ViewSelection";
import { MODEL_TYPE } from "./../constants";
import { useKeyPress } from "../custom-hooks/useKeyPress";

const useStyles = makeStyles({
	toolbarContainer: {
		display: "flex",
		flexWrap: "wrap",
		width: "100%",
		height: "inherit",
		backgroundColor: "rgb(41, 56, 70)",
		padding: "0.5rem 0.75rem",
		".modern-dark &": {
			backgroundColor: "#323232",
		},
	},
	toolItemView: {
		color: "#fff",
		padding: "5px 10px",
	},
	crudIcon: {
		height: "1em",
		width: "1em",
		fontSize: 14,
		cursor: "pointer",
	},
	selectionContainer: {
		flex: 1,
		display: "flex",
		alignItems: "center",
		flexWrap: "wrap",
	},
	toolbarActions: {
		display: "flex",
		alignItems: "center",
	},
	disableBtn: {
		color: "#555 !important",
	},
});

function isStudioView(view) {
	if (view && view.xmlId && view.xmlId.indexOf("studio-") === 0) {
		return true;
	}
	return false;
}

function ToolbarButton(props) {
	const classes = useStyles();
	return (
		<IconButton
			className={classes.toolItemView}
			classes={{ disabled: classes.disableBtn }}
			{...props}
		/>
	);
}

function Toolbar({
	update,
	saveView,
	modelType,
	saveCustomFieldView,
	removeView,
	model,
	onNew,
	reset,
	...props
}) {
	const classes = useStyles();
	const save = React.useCallback(() => {
		saveView();
		if (modelType === MODEL_TYPE.BASE) {
			saveCustomFieldView();
		}
	}, [saveView, saveCustomFieldView, modelType]);
	const isRemoveDisabled = !(
		props.extensionView ||
		isStudioView(props.selectedView) ||
		(modelType === MODEL_TYPE.CUSTOM && Boolean(props.customModel))
	);
	const isNewDisabled = modelType === MODEL_TYPE.BASE && !model;
	const isSaveDisabled = Object.keys(props.errorList).length > 0;

	useKeyPress(["s"], save);

	return (
		<div className={classes.toolbarContainer}>
			<div className={classes.toolbarActions}>
				<ToolbarButton
					className={classes.toolItemView}
					onClick={props.onSidePanelToggle}
				>
					<i className={classnames("fa fa-bars", classes.crudIcon)} />
				</ToolbarButton>
				{modelType !== MODEL_TYPE.BASE && (
					<ToolbarButton
						className={classes.toolItemView}
						onClick={onNew}
						disabled={isNewDisabled}
					>
						<i className={classnames("fa fa-plus", classes.crudIcon)} />
					</ToolbarButton>
				)}
				<ToolbarButton
					className={classes.toolItemView}
					onClick={save}
					disabled={isSaveDisabled}
				>
					<i className={classnames("fa fa-floppy-o", classes.crudIcon)} />
				</ToolbarButton>
				{modelType === MODEL_TYPE.CUSTOM && (
					<ToolbarButton
						className={classes.toolItemView}
						onClick={removeView}
						disabled={isRemoveDisabled}
					>
						<i className={classnames("fa fa-trash-o", classes.crudIcon)} />
					</ToolbarButton>
				)}
			</div>
			<div className={classes.selectionContainer}>
				<ViewSelection
					model={model}
					update={update}
					modelType={modelType}
					reset={reset}
					{...props}
				/>
			</div>
			<div className={classes.toolbarActions}>
				<ToolbarButton
					className={classes.toolItemView}
					onClick={() => props.refresh()}
					disabled={!props.customModel && !model}
				>
					<i className={classnames("fa fa-refresh", classes.crudIcon)} />
				</ToolbarButton>
				<ToolbarButton
					className={classes.toolItemView}
					onClick={() => props.undo()}
					disabled={props.pastLength <= 0}
				>
					<i className={classnames("fa fa-reply", classes.crudIcon)} />
				</ToolbarButton>
				<ToolbarButton
					onClick={() => props.redo()}
					disabled={props.futureLength <= 0}
				>
					<i className={classnames("fa fa-share", classes.crudIcon)} />
				</ToolbarButton>
			</div>
		</div>
	);
}

export default React.memo(Toolbar);
