import React from "react";
import Grid from "./Grid";
import { PANEL_TYPE } from "../constants";

/**
 * Form Component
 * Used As Main Container i.e. <form />
 */
function FormComponent({ attrs = {}, items = [], design, widgets, ...rest }) {
	// render top level items as Widgets using Context FormConsumer
	const rootPanel = React.useMemo(() => {
		const sidebar = items.filter(
			(k) => widgets[k] && ["true", true].includes(widgets[k].sidebar)
		);
		const mainItems = items.filter(
			(k) => widgets[k] && !["true", true].includes(widgets[k].sidebar)
		);
		const hasSidebar = sidebar.length > 0;

		return hasSidebar
			? {
					items: [
						{
							layout: PANEL_TYPE.grid,
							items: mainItems,
							colSpan: 9,
						},
						{
							layout: PANEL_TYPE.grid,
							items: sidebar,
							colSpan: 3,
						},
					],
			  }
			: {
					items: [
						{
							layout: PANEL_TYPE.grid,
							items: mainItems,
							colSpan: 12,
						},
					],
			  };
	}, [items, widgets]);

	return (
		<div className="flex-container">
			{rootPanel.items.map(({ items, colSpan, ...attrs }, i) => (
				<div className={`flex-item span-${colSpan}`} key={i}>
					<Grid
						key={i}
						design={design}
						className="form-body"
						items={items}
						attrs={attrs}
						_type={rest._type}
						widgets={widgets}
						errorList={rest.errorList}
						canRemove={rest.canRemove}
					/>
				</div>
			))}
		</div>
	);
}

export default FormComponent;
