import React from "react";
import { makeStyles } from "@material-ui/core/styles";
import Button from "@material-ui/core/Button";
import Grid from "@material-ui/core/Grid";
import Input from "@material-ui/core/Input";
import ChevronLeft from "@material-ui/icons/ChevronLeft";
import ChevronRight from "@material-ui/icons/ChevronRight";
import Typography from "@material-ui/core/Typography";
import Dialog from "@material-ui/core/Dialog";
import DialogActions from "@material-ui/core/DialogActions";
import DialogContent from "@material-ui/core/DialogContent";
import DialogTitle from "@material-ui/core/DialogTitle";
import useMediaQuery from "@material-ui/core/useMediaQuery";
import List from "@material-ui/core/List";
import ListItem from "@material-ui/core/ListItem";
import ListItemText from "@material-ui/core/ListItemText";
import { useTheme } from "@material-ui/core/styles";
import IconButton from "@material-ui/core/IconButton";

const useStyles = makeStyles((theme) => ({
	root: {
		width: "100%",
		backgroundColor: theme.palette.background.paper,
		maxHeight: 300,
		overflow: "auto",
	},
	input: {
		width: "80%",
		marginRight: 10,
	},
	titleHeader: {
		borderBottom: "0.5px solid #ddd",
	},
	listItem: {
		borderBottom: "1px solid #ddd",
		paddingTop: 10,
		paddingBottom: 10,
	},
	closeText: {
		textTransform: "capitalize",
	},
}));

export default function ResponsiveDialog(props) {
	const {
		list = [],
		onChange,
		handleClose,
		offset,
		total,
		displayField,
		getOptionLabel,
	} = props;
	const theme = useTheme();
	const classes = useStyles();
	const fullScreen = useMediaQuery(theme.breakpoints.down("sm"));

	const handleItemClick = React.useCallback(
		(item) => {
			onChange(item);
			handleClose();
		},
		[onChange, handleClose]
	);

	const canNext = offset + list.length < total;
	const canPrev = offset > 0;

	const getLabel = (item) => {
		return getOptionLabel ? getOptionLabel(item) : item[displayField || "name"];
	};

	return (
		<div>
			<Dialog
				fullScreen={fullScreen}
				maxWidth="sm"
				fullWidth={true}
				open={props.open}
				onClose={props.handleClose}
				aria-labelledby="responsive-dialog-title"
			>
				<DialogTitle
					id="responsive-dialog-title"
					className={classes.titleHeader}
				>{`Search ${props.title || "item"}`}</DialogTitle>
				<DialogContent>
					<Grid container>
						<Input
							className={classes.input}
							value={props.searchText}
							onChange={(e) => props.setSearchText(e.target.value)}
						/>
						<IconButton onClick={props.onPrevious} disabled={!canPrev}>
							<ChevronLeft />
						</IconButton>
						<IconButton onClick={props.onNext} disabled={!canNext}>
							<ChevronRight />
						</IconButton>
					</Grid>
					<List className={classes.root}>
						{list.map((item, i) => (
							<ListItem
								className={classes.listItem}
								key={i}
								role={undefined}
								dense
								button
								onClick={() => handleItemClick(item)}
							>
								<ListItemText id={i} primary={getLabel(item)} />
							</ListItem>
						))}
					</List>
				</DialogContent>
				<DialogActions>
					<Button autoFocus onClick={props.handleClose} color="primary">
						<Typography className={classes.closeText}>Close</Typography>
					</Button>
				</DialogActions>
			</Dialog>
		</div>
	);
}
